import { _decorator, Component, Sprite, Label, SpriteFrame } from 'cc';
const { ccclass, property } = _decorator;
import { CardData } from './CardData';

@ccclass('Card')
export class Card extends Component {
    cardSprite: Sprite = null;

    rankLabel: Label = null;

    suitLabel: Label = null;

    // Optional: store card data for reference
    private suit: string = '';
    private rank: string = '';
    private values: number[] = [];

    protected start(): void {
        this.cardSprite = this.node.getComponentInChildren(Sprite)!;
    }

    public init(cardData: CardData) {
        this.suit = cardData.suit;
        this.rank = cardData.rank;
        this.values = cardData.values;

        if (this.cardSprite) {
            this.cardSprite.spriteFrame = cardData.spriteFrame;
        }

        this.node.name = `${cardData.rank}_of_${cardData.suit}`;
    }

    // Optional: expose card value logic
    public getValues(): number[] {
        return this.values;
    }

    public getName(): string {
        return `${this.rank} of ${this.suit}`;
    }
}